@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700,800&display=swap);
* {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: 'Montserrat', sans-serif;
}

body {
    background-color: #F2F2F2;
    max-width: 100%;
    width: 100%;
    width: 100vw;
}
a{
    outline: none;
    cursor: none;
    text-decoration: none;
}
.container{
    width: 100%;
    width: 100vw;
    display: grid;
    grid-template-areas:
        'logo'
        'main'
        'image';
}

.logo {
    grid-area: logo;
    margin: auto;
}

.main { 
    grid-area: main;
    text-align: center;
}

.title {
    padding: 3vw;
    font-size: 3rem;
    font-weight: 800;
}

.image {
    grid-area: image;
}


img.main-image-mobile {
    max-width: 100vw;
}

img.main-image {
    visibility: hidden;
}

.logo-image{
    width: 50vw;
    padding: 5vw;
}

button {
    display: flex;
    justify-content: space-around;
    margin: auto;
    padding: 0.8rem;
    border-radius: 3rem;
    background-color: #15573F;
    color: #FFF;
    width: 10rem;
    font-weight: 500;
    font-size: 0.9rem;
}




.option-water:hover{
    background-color: #7BAF9E !important;
}

@media (min-width:801px) {
    body{
        overflow: hidden;
    }

    .container {
        width: 80%;
        height: 700px;
        margin: auto;
        display: grid;
        grid-template-areas:
            'logo image'
            'main image';
        grid-template-columns: 1fr 2fr;
        grid-template-rows: auto 3fr;
    }

    img.main-image-mobile {
        visibility: hidden;
        height: 0;
        width:0;
    }

    img.main-image {
        visibility: visible;
        max-width: 80vw;
        margin-top: 20px;
        margin-left: -100px;
    }

    .logo{
        padding: 3vw;
        margin: 0;
        min-width: 330px;
    } 
    
    .main{
        padding-left: 3vw;
        text-align: left;
    }

    .logo-image{
        width: 10rem;
        padding: 0;
    }

    .title {
        padding: 3rem 0 2rem;
    }

    button{
        margin: 0;
    }

    #btn-start:hover{
        cursor: pointer;
        outline: none;
        box-shadow: 1px 1px 9px -1px rgba(21,87,63,1);
    }
}

@media (max-width:800px) {
    body {
        overflow-x: hidden;
    }
    img.main-image {
        height: 0;
        width:0;
    }
}

.containerQuiz {
    margin: auto;
    width: 90%;
    width: 90vw;
    display: grid;
    grid-template-areas:
        'logo'
        'main'
        'options'
        'navigation';
}

.logo {grid-area: logo;}
.main {grid-area: main;}

.options {
    grid-area: options;
    text-align: center;
}

.navigation {
    grid-area: navigation;
    display: grid;
}

.main img {
    width: 35vw;
    max-width: 11rem;
}

.main h2{
    font-weight: 500;
    line-height: 2.5rem;
    padding: 1.6rem;
    padding-bottom: .5rem;
    font-size: 1.7rem;
    color: #6E6E6E;
    text-align: center;
}

.btn-home {
    margin: auto;
}

.optionButton {
    color: #6E6E6E;
    margin: auto;
    width: 80vw;
    border-radius: .5rem;
    padding: .8rem;
    margin-bottom: 1.5rem;
    background-color: #fff;
    box-shadow: 0px 21px 68px -14px rgba(110,110,110,0.71);
}

p{
    color: #6E6E6E;
    font-weight: 500;
    line-height: 2.5rem;
    padding: 0 1rem 1.6rem 1rem;
}

.buttonPrev {
    width: 7rem;
    text-align: center;
    border-radius: 3rem;
    padding: 1rem;
    color: #15573F;
    border: 1px solid #15573F;
    grid-area: prev;
}

.buttonNext {
    width: 7rem;
    text-align: center;
    border-radius: 3rem;
    padding: 1rem;
    color: #fff;
    background-color: #15573F;
    grid-area: next;
}

.selected.option-sun{
    background-color: #FD9872;
    color: #fff;
    font-weight: 700;
box-shadow: 0px 21px 27px -22px rgba(253,152,114,1);
}

.selected.option-pets{
    background-color: #FD9872;
    color: #fff;
    font-weight: 700;
box-shadow: 0px 21px 27px -22px rgba(253,152,114,1);
}
        
.selected.option-water{
    background-color: #7BAF9E;
    color: #fff;
    box-shadow: 1px 2px 9px -1px rgba(123,175,158,1);
}

@media (min-width: 801px){
    .containerQuiz{
        display: grid;
        width: 100vw;
        grid-template-areas:
            'logo main empty'
            'logo question empty'
            'logo navigation empty';
            grid-template-columns: 1fr 5fr 1fr;
    }

    .empty {
        grid-area: empty;
        width: 10vw;
        min-width: 10vw;
        max-width: 10vw;
    }
    .question{
        grid-area: question;
        display: flex;
        margin: auto;
        padding: 1rem;
    }

    .optionButton{
        padding: 1.6rem;
        margin: 1rem;
        width: 9rem;
        height: 8rem;
        font-size: .9rem;
    }

    .optionButton:hover{
        font-weight: 700;
        color: #fff;
        background-color: #FD9872;
    }

    .optionButton, .buttonNext, .buttonPrev:hover{
        cursor: pointer;
    }

    .logo-quiz{
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
        padding: 0;
        margin: auto;
        margin-top: 10rem;
        width: 10vw;
        min-width: 10vw;
        max-width: 10vw;
    }

    .main-quiz{
        padding: 0;
        margin: auto;
    }

    .main-quiz img{
        display: flex;
        margin: auto;
        max-width: 8rem;
        padding-top: 3rem;
    }
    
    .main h2{
        max-width: 30rem;
    }

    .navigation{
        display: flex;
        justify-content: space-around;
        grid-template-areas:
        'prev next';
    }
    .buttonPrev:hover{
        background-color: #15573F;
        color: #fff;
    }

    .buttonNext{
        background-color: #fff;
        color: #15573F;
        border: 1px solid #15573F;
    }

    .buttonNext:hover{
        background-color: #15573F;
        color: #fff;
    }
    
}

@media (max-width:800px){
    
    .navigation {
        margin: auto;
        grid-template-areas:
            'next'
            'prev';
    }
    
    .buttonNext{
        margin: 1rem;
    }

    .buttonPrev{
        margin: 1rem;
    }
}
svg.iconPlantList{
    width: 24px;
    height: 24px;
}
.container-results {
    margin: auto;
    width: 100%;
    width:100vw;
    display: grid;
    grid-template-areas:
        'logo'
        'main'
        'plants';
}

.logo {grid-area: logo;}

.main {
    grid-area: main;
}

.main-results img {
    display: flex;
    margin: auto;
    padding-top:1rem;
}

.main-results h1 {
    padding:1rem;
    text-align: center;
}

.plants {
    grid-area: plants;
}

h1{
    padding: 2rem;
    font-size: 3rem;
    font-weight: 800;
}

@media (min-width: 801px){
    a{
        cursor: pointer;
    }
    .container-results {
        width:100vw;
        display: grid;
        grid-template-areas:
            'logo main empty'
            'logo plants empty';
            grid-template-columns: 1fr 5fr 1fr;
        overflow-y: auto;
    }

    .empty {
        grid-area: empty;
        width: 10vw;
        min-width: 3rem;
        max-width: 10vw;
    }

    .logo-results{
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
        padding: 0;
        margin: auto;
        margin-top: 10rem;
        width: 10vw;
        min-width: 8rem;
        max-width: 10vw;
    }

    .main-results{
        min-width: 620px;
        padding-top: 3rem;
    }

    .plant-group{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        padding: 3rem;
    }
}

@media (max-width: 800px){
    .plant-group{
        overflow-x: scroll;
        overflow-y: hidden;
        white-space: nowrap;
        max-width: 100vw;
    }
}
.container-purchase{
    padding: 0;
    margin: auto;
    width: 100%;
    width: 100vw;
    display: grid;
    grid-template-areas:
        'logo'
        'plant'
        'form';
}

.plant{
    grid-area: plant;
}

.logo{grid-area: logo;}

.logo.logo-purchase{
    min-width: 0;
    padding: 0;
}

.plant h1{
    padding: 0 2rem;
}
.plant h2{
    color: #d6d6d6;
    font-size: 2rem;
    font-weight: 500;
    padding: 0 2rem;
}
.plant img{
    padding: 0 2rem;
}
.plant .icon-purchase{
    padding: 0.2rem 0;
}

.plant .specs{
    display: flex;
    flex-direction: column;
    padding: 0 2rem;
    color: #6E6E6E;
}

.plant .icon-purchase{
    display: flex;
    align-items: center;
}

.plant .iconPlantList{
    padding: 0.3rem;
}

.form{
    grid-area: form;
    background-color: #f2f2f2;
}
.form .description{
    padding: 0 2rem;
    padding-bottom: 2rem;
}
.form h1{
    font-size: 2.3rem;
}
.form label{
    font-weight: 700;
    color: #6E6E6E;
}
.form input{
    border-radius:10rem;
    margin: 0.2rem 0;
    outline: none;
}


@media(min-width:801px){
    .container-purchase {
        width:100vw;
        display: grid;
        grid-template-areas:'logo plant form empty';
        padding-top: 5rem;
    }
    
    .empty{
        grid-area: empty;
    }

    .logo{
        width: 10vw;
    }
    .logo.logo-purchase{
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
        margin: auto;
        margin-top: 5em;
    }
    .plant{
        width: 40vw;
    }
    .plant .h1{
        padding: 0 5rem;
    }
    .plant .h2{
        padding: 0 5rem;
    }

    .form{
        width: 30vw;
    }
    .form h1{
        padding-bottom: 1rem;
    }
    .form label{
        padding: 2rem;
    }
    .form input{
        width: 70%;
        margin-left: 10%;
        padding: .5rem 1rem;
    }

    
    .form input[type=submit]{
        margin-top: 1rem;
        width:7rem;
        float: right;
        padding:0.9rem;
        margin-right: 10%;
        background-color: #fff;
        color: #15573F;
        border: 1px solid #15573F;
        outline: none;
    }
    .form input[type=submit]:hover{
        background-color: #15573F;
        color: #fff;
        cursor: pointer;
    }
    .form img{
        display: flex;
        width:12rem;
        margin: auto;
    }
}

@media(max-width:800px){
        
    .plant img{
        display: flex;
        margin: auto;
    }
    
    .plant h1{
        padding: 0 10vw;
    }
    .plant h2{
        padding: 0 10vw;
    }
    .plant img{
        padding: 0 10vw;
    }
    .plant .icon-purchase{
        padding: 0.2rem 0;
    }

    .plant .specs{
        padding: 0 10vw;
    }
    
    .plant{
        padding-top: 2rem;
        padding-bottom: 2rem;
    }

    .form .description{
        font-size: 1.2rem;
        line-height: 1.5rem;
    }

    .form label{
        padding: 5vw 10vw;
    }
    
    .form input[type=submit]{
        width:90vw;
        margin: 1rem 5vw 2rem 5vw;
        padding: 4vw 5vw;
        background-color: #15573F;
        color: #fff;
        outline: none;
    }

    .form input[type=text]{
        width: 80vw;
        margin: 0 5vw 0 5vw;
        padding: 4vw 5vw;
    }

    
    .form img{
        width:60vw;
        margin: 0 20vw 3rem 20vw;
    }
}
